@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700');
@import "variables";
@import "mixin";
@import "sidebar";
@import "header";
@import "overview";

body {
	display: flex;
	margin: 0;
	background-color: whitesmoke;
	font-family: 'Roboto';
}

p {
	margin: 0;
}

.ember-view {
	display: flex;
	width: 100%;
}

.container {
	display: flex;
	width: auto;
	flex: 1 0 640px;
	align-items: center;
	height: 100%;
    overflow-x: hidden;
    flex-direction: column;
}

.content {
	overflow-y: auto;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}

input::-webkit-input-placeholder {
	transition: all 0.2s ease-in-out;
}

input {
  margin: 32px 24px;
  width: 200px;
  display: block;
  border: none;
  padding: 10px 0 4px 0;
  border-bottom: solid 1px $primary__color;
  transition: all 0.2s cubic-bezier(.64,.09,.08,1);
  color: darken($primary__color, 20%);
  &:focus, &:valid {
    box-shadow: none;
    outline: none;
    background-position: 0 0;
    &::-webkit-input-placeholder {
      color: $primary__color;
      font-size: 11px;
      transform: translateY(-14px);
	  visibility: visible !important;
	  transition: all 0.2s ease-in-out;
    }
  }
}

