.overview {
    display: flex;
    width: 100%;
    height: 100%;
    &__list {
        display: flex;
        flex-grow: 7;
        flex-direction: column;
        background-color: white;
        margin: 32px;
        @include shadow($shadow__level--1);
        &--item {
            display: flex;
            width: 100%;
            height: 40px;
            align-items: center;
            border-bottom: 1px solid lightgray;
            padding: 0 16px;
            box-sizing: border-box;
        }
    }
    &__header {
        display: flex;
        width: 100%;
        height: 48px;
        align-items: center;
        border-bottom: 1px solid red;
        padding: 0 16px;
        box-sizing: border-box;
        &--item {
            display: flex;
            flex-grow: 1;
        }
    }
    &__right {
        display: flex;
        flex-direction: column;
        flex: 5 0 200px;
    }
    &__filter {
        flex: 8 0;
        background-color: white;
        margin: 32px 32px 16px 0;
        @include shadow($shadow__level--1);
    }
    &__reservations {
        flex: 4 0;
        background-color: white;
        margin: 16px 32px 32px 0;
        @include shadow($shadow__level--1);
    }
}

.overview__list--name,
.overview__list--facility,
.overview__list--capacity,
.overview__list--outlets,
.overview__header--item {
    display: flex;
    flex: 1 0 25%;
}
